import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import '../App.css';

  export default function Navigation() {
    return (
      <Router>
        <div>
          <nav className="navigation">
            <ul>
              <li>
                <Link to="/">Sākums</Link>
              </li>
              <li>
                <Link to="/hobiji">Hobiji</Link>
              </li>
              <li>
                <Link to="/darbi">Darbi</Link>
              </li>
            </ul>
          </nav>
  
          {/* A <Switch> looks through its children <Route>s and
              renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/hobiji">
              <Hobiji />
            </Route>
            <Route path="/darbi">
              <Darbi />
            </Route>
            <Route path="/">
              <Sakums />
            </Route>
          </Switch>
        </div>
      </Router>
    );
  }
  
  function Sakums() {
    return (
      <div className="text starting">
        <p>Šeit Jūs varēsiet iepazīties ar manu portfolio</p>
      </div>
    ) ;
  }
  
  function Hobiji() {
    return (
      <div className="text">
        <h2>Hobiji</h2>
        <p>Mani hobiji sastāv no jautrām aktivitātēm draugu lokā neatkarīgi no tā, vai tās būtu galda spēles, vai futbols, vai geocatchings, vai jebkāda cita aktivitāte, galvenais, ka tas laiks tiek izbaaudīts ar draugiem vai ģimeni.</p>
      </div>
    ) 
  }
  
  function Darbi() {
    return (
      <div className="text">
        <h2>Darbi</h2>
        <p>Esmu savas dzīves laikā strādājis vairākos darbos un jomās - gan par pavāru, gan mākleri, gan jurista palīgu/konsultantu, gan ārštata web dizaineri, gan biroja administratoru/tehnisko darbinieku Maltā(valstī, nevis Latvijas pagastā) utt., bet vienmēr visos uzņēmumos esmu paralēli interesējies par programmēšanu un vienmēr kolēģi man arī ir teikušu, ka tas ir īstais virziens, kur man jādodās.</p>
      </div>
    ) 
  }